.sidebar-buttons {
  width: 196px;
  height: 24px;
  transform: rotate(0deg);
  opacity: 1;
  gap: 12px;
  border-radius: 4px;
  padding: 4px 6px;
  background: rgba(105,76,128,1);
  border: 0px;

   /* TEXT STYLING */
  font-family: 'Avenir Next', sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  color: rgba(252, 250, 255, 1);
  line-height: 100%;
  letter-spacing: 0;   
}

.sidebar-buttons:hover {
  background: rgba(255, 255, 255, 1);
  color: rgba(105, 76, 128, 1);
  cursor: pointer;
}