.card {
  width: 212px;
  height: 151px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  opacity: 1;
  border-radius: 8px;
  padding: 8px;
  transform: rotate(0deg);
  background: rgba(105,76,128,1);
}

.features {
  width: 44px;
  height: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  opacity: 1;
  border-radius: 4px;
  padding: 2px 6px;
  background: rgba(53, 36, 66, 1);
}

.features-text {
  font-family: 'Avenir Next', sans-serif;
  font-weight: 500; 
  font-style: normal; 
  font-size: 8px;
  line-height: 100%;
  letter-spacing: 0;
  color: rgba(230, 228, 233, 1);
  padding: 0;
}

